import Vue from 'vue'
import VueRouter from 'vue-router'
//import Resoluciones from '../views/Resoluciones'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/resoluciones/2019'
  },
  {
    path: '/resoluciones/:year',
    name: 'Resoluciones',
    component: () => import('../views/Resoluciones.vue')
  },
  {
    path: '/integrantes',
    name: 'Integrantes',
    component: () => import('../views/Integrantes.vue')
  },
  {
    path: '/licencias',
    name: 'Licencias',
    component: () => import('../views/Licencias.vue')
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
